<template>
  <v-app style="background: #111722">
    <v-main class="pt-5">
      <section id="Actors">
        <v-container>
          <div class="main_heading special_font">
            <h1>
              <span>Popular Actors</span>
            </h1>
            <span class="special_design">
              <i></i>
              <i></i>
              <i></i>
            </span>
          </div>
          <!--  -->
          <template v-if="true">
            <v-row class="mt-4">
              <v-col
                v-for="(person, i) in actors"
                :key="i"
                cols="6"
                sm="3"
                v-show="person.profile_path !== null"
              >
                <v-hover v-slot="{ hover }">
                  <div>
                    <v-img
                      :src="`https://image.tmdb.org/t/p/w500/${person.profile_path}`"
                      width="100%"
                      min-height="300"
                      height="300"
                      class="d-flex align-end justify-center"
                    >
                      <template #placeholder>
                        <v-card color="#111722" height="300">
                          <v-skeleton-loader
                            type="image, image"
                            v-bind="attrs"
                          />
                        </v-card>
                      </template>
                      <v-slide-y-reverse-transition>
                        <v-card v-if="hover" class="transparent">
                          <v-card-text
                            class="d-flex align-center justify-center"
                            style="gap: 5px"
                          >
                            <v-chip
                              small
                              :color="
                                person.popularity > 95
                                  ? '#ffab31'
                                  : person.name == 'Johnny Depp' ||
                                    person.name == 'Tom Cruise' ||
                                    person.name == 'Cillian Murphy' ||
                                    person.name == 'Jason Statham'
                                  ? 'error lighten-1'
                                  : ''
                              "
                              :class="[
                                person.popularity > 95 ? 'white--text' : '',
                              ]"
                            >
                              <span>{{
                                person.name
                                  .split(" ")
                                  .filter((el, i) => {
                                    return i < 2 ? el : "";
                                  })
                                  .join(" ")
                              }}</span>
                            </v-chip>
                          </v-card-text>
                        </v-card>
                      </v-slide-y-reverse-transition>
                    </v-img>
                  </div>
                </v-hover>
              </v-col>
              <!-- {{ persons }} -->
            </v-row>
          </template>
          <!--  -->
        </v-container>
      </section>
    </v-main>
  </v-app>
</template>

<script>
export default {
  data: () => ({
    actors: [],
    firstLoad: true,
    attrs: {
      class: "mb-6",
      boilerplate: false,
      elevation: 2,
    },
  }),
  mounted() {
    this.getActors();
  },
  methods: {
    async getActors() {
      await this.$axios
        .get(
          `https://api.themoviedb.org/3/person/popular?api_key=${this.$apiKey}&language=en-US`
        )
        .then((resp) => {
          this.actors = resp.data.results;
          // console.log("actors", resp.data.results);
        });
    },
  },
  created() {
    const readyHandler = () => {
      if (document.readyState == "complete") {
        setTimeout(() => {
          this.firstLoad = false;
        }, 1500);
        document.removeEventListener("readystatechange", readyHandler);
      }
    };
    document.addEventListener("readystatechange", readyHandler);
    readyHandler();
  },
};
</script>

